<template>
	
  	<div class="create_wrapper">
  		<h1 class="title">&ensp;&ensp;权限管理</h1>
		<div class="add_created" >
	    	<i class="el-icon-plus" @click="addRoleTree"></i>
	    	<p>添加角色</p>
	    </div>
	    <!--创建角色弹出框-->
	    <el-dialog
		    title="请创建角色"
		    :visible.sync="createDialogVisible"
		    width="40%"
		    :before-close="handleClose">
		 	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		 		<el-form-item label="角色名称:" prop="name">
			    	<el-input v-model="ruleForm.name" placeholder="角色名称" style="width:90%;"></el-input>
			    </el-form-item>
			    <el-form-item label="功能权限:">
			    	<el-tree
						:data="cascaderData"
						default-expand-all
						node-key="id"
						style="height: 150px;overflow:auto;"
						:props="defaultProps">
					</el-tree>
				</el-form-item>
			</el-form>
		    <span slot="footer" class="dialog-footer">
			    <el-button type="primary" @click="addTreeFun('ruleForm')">保存</el-button>
		  	</span>
		</el-dialog>
		
		
  	</div>
</template>

<script>
	import cascader from './cascader'
	import {
	  	getUserInfo,
	  	getUsers
	} from 'utils/auth';
	import {
		leftMenuPort
	} from 'api/model/leftMenu';   //权限树
  	export default {
	    name: 'creatRole',
	    
	    data() {
	      	return {
		        createDialogVisible: false,
				cascaderData:[],
				//功能tree
				defaultProps: {
		          children: 'subMenu',
		          label: 'MENU_NAME'
		        },
				//创建者弹出框
				ruleForm:{
					name:''
				},
				rules: {
				 	name: [
			            { required: true, message: '请输入角色名称', trigger: 'blur' },
			            { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
			        ],
			       
				}
	      	}
	    },
	    computed: {
	    	
	    },
	    methods:{
	    	//添加角色确定
	    	addTreeFun(formName){

	    		this.$refs[formName].validate((valid) => {
					if (valid) {
	    				this.createDialogVisible = false
	    				this.$router.push({
	    					name:'binder_list'
	    				})
					} else {
						console.log('error submit!!');
						return false;
					}
				});
	    	
	    	},
	    	//添加权限树
	    	addRoleTree(){
	    		this.createDialogVisible = true
	    		let req = {
		    		USER_ID:getUsers(),  // 
		    		RIGHTS:'83074214435356785597684947861110784',
		    		PLATFORM:'1'    //系统平台
	    	    }
   			
		    	leftMenuPort(req).then( data => {
		    	 	this.cascaderData = data
	//		  		console.log('菜单::'+JSON.stringify(data))
			  		
			    })
	    	},
	    	handleClose(done) {
		        this.$confirm('确认关闭？')
		          .then(() => {
		            done();
		          })
		          .catch(() => {});
		      	}
			}
  	}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scope">
	.create_wrapper{
		height: 100%;
		background: #fff;
		padding-bottom: 20px;
		.title{
			border-left:2px solid #409EFF;
			font-size: 1.6rem;
			border-bottom: 1px solid #eee;
			padding: 10px 0;
		}
		.add_created{
			background: #EEEEEE;
			width: 150px;
			padding:15px 0 3px 0 ;
			margin-left: 10px;
			border-radius: 6px;
			text-align: center;
			/*font-size:60px ;*/
			margin-top: 15px;
			i{
				cursor:pointer ;
				font-size:80px ;
			}
			p{
				font-size: 1.4rem;
			}
		}
	}
</style>



